@import url('https://fonts.googleapis.com/css2?family=New+Amsterdam&display=swap');

body{
  background: #F8F9FD;
}
.news{
  list-style: none;
}
.heading{
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: New Amsterdam;
  border-width: 1px;
  border-style: solid;
  border-color: #262931 transparent;
}
.heading h1{
  font-size: 38px;
}
.nav-select{
  width: 120px;
  height: 48px;
  border: none;
  font-size: 18px;
}
.news-container img{
  width: 70%;
  height: 70%;
}
.news-item{
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 20px;
}
.news-item h2{
  font-size: 46px;
  font-family: New Amsterdam;
}
.options{
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 30px;
  padding: 30px;
}
.options ul{
  padding: 20px;
  display: flex;
  justify-content: center;
  gap: 50px;
}
.options ul li{
  display: flex;
  justify-content: center;
}
.links{
  text-decoration: none;
  color: #fff;
  background: #262931;
  display: flex;
  justify-content: center;
  border: 1px solid #262931;
  padding: 10px;
  width: 120px;
  border-radius: 5px;
  transition: .3s ease;
  font-family: New Amsterdam;
}
.links:hover{
  transform: scale(1.5);
  border: none;
  background: #F8F9FD;
  font-size: 18px;
  color:#262931
}
.search-input{
  font-family: New Amsterdam;
  width: 60%;
  height: 20px;
  padding: 10px;
  border-radius: 10px;
  border: 1px solid #262931;
  background: #F8F9FD;
  font-size: 16px;
}
.filtering-container{
  display: flex;
  justify-content: center;
  margin: 20px;
}
.urdu{
  direction: rtl; /* Ensure text direction is right-to-left */
  text-align: right; /* Align text to the right */
  font-family: 'Noto Sans Urdu', sans-serif; /* Use a font that supports Urdu */
}